body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282828;
  font-family: avenir;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: "digital";
  src: local("Digital"), url("./digital.ttf") format("truetype");
}

@font-face {
  font-family: "avenir";
  src: local("Avenir"), url("./avenir.otf") format("truetype");
}

#drum-machine {
  margin: auto;
  margin-top: 15%;
  background-color: #aaa9ad;
  /* background: linear-gradient(to bottom, #000 60%, #323232 100%); */

  display: flex;
  width: 700px;
  justify-content: space-between;
  border-style: groove;
  font-family: avenir;
}
#keys {
  display: grid;
  grid-template-columns: repeat(3, 120px);
  grid-template-rows: repeat(3, 100px);
  column-gap: 10px;
  row-gap: 10px;
  width: 50%;
  padding: 30px;
}

#display {
  color: black;
  text-align: center;
  margin: auto;
  margin-top: 20px;
}

#current {
  background-color: #171717;
  font-size: 40px;
  height: 60px;
  width: 200px;
  padding-top: 20px;
  font-family: digital;
  color: cyan;
}

.drum-pad {
  font-size: 30px;
  text-align: center;
  box-shadow: 1px 1px 5px 2px cyan;
  background-color: #171717;
  color: #cdcdcd;
  font-family: avenir;
}

button:hover {
  box-shadow: 1px 1px 20px 1px blue;
}

/* The switch - the box around the slider */
.switch {
  margin-top: 0;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #171717;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: cyan;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
